.center-video-show {
  margin-top: 5px;
  background-color: #F6F6F7;
  height: 100%;
  width: 100%;
}
.center-video-show .center-layout {
  height: 100%;
}
.center-video-show .center-layout .center-header {
  width: 100%;
  height: 45px;
  padding: 0;
  background-color: #FFFFFF;
}
.center-video-show .center-layout .center-header .export-btn {
  width: 80px;
  height: 30px;
  background: #F6F6F7;
  border-radius: 3px;
  border: none;
  color: black;
  padding: 0;
  float: right;
  margin-top: 8px;
}
.center-video-show .center-layout .center-canvas {
  background: #F6F6F7;
  height: calc(100% - 90px);
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}
.center-video-show .center-layout .center-canvas .canvas-video-display {
  background: #FFFFFF;
  height: 95%;
  width: 95%;
  display: inline-block;
  justify-content: space-between;
  align-items: center;
  flex-direction: column;
}
.center-video-show .center-layout .center-canvas .canvas-video-display .video-chart {
  overflow: hidden;
  position: relative;
  height: 95%;
  width: 100%;
  display: inline-block;
  justify-content: center;
  align-items: center;
}
.center-video-show .center-layout .center-canvas .canvas-video-display .video-chart > svg {
  zoom: 0.76;
}
.center-video-show .center-layout .center-canvas .canvas-video-display #caption {
  display: block;
  -webkit-transform: scale(0.8);
          transform: scale(0.8);
  height: 20%;
  width: 75%;
  display: flex;
  align-items: center;
  justify-content: center;
}
.center-video-show .center-layout .center-canvas > div {
  margin-top: 20px;
}
.center-video-show .center-layout .center-footer-player {
  height: 45px;
  display: flex;
  padding: 0;
  width: 100%;
  background: #FFFFFF;
}
.center-video-show .center-layout .center-footer-player .left-empty {
  width: 30%;
  height: 100%;
}
.center-video-show .center-layout .center-footer-player .player-btn {
  height: 100%;
  width: 40%;
  display: flex;
  justify-content: center;
}
.center-video-show .center-layout .center-footer-player .player-btn .pause-btn,
.center-video-show .center-layout .center-footer-player .player-btn .play-btn,
.center-video-show .center-layout .center-footer-player .player-btn .next-btn,
.center-video-show .center-layout .center-footer-player .player-btn .last-btn {
  height: 100%;
  width: 15%;
  display: flex;
  align-items: center;
  justify-content: center;
}
.center-video-show .center-layout .center-footer-player .time-display {
  overflow: hidden;
  height: 100%;
  width: 30%;
  display: flex;
  justify-content: flex-end;
}
.center-video-show .center-layout .center-footer-player .time-display .time-wrapper {
  font-size: 16px;
  display: flex;
  align-items: center;
  float: right;
  margin-right: 10px;
  vertical-align: middle;
}
